.nav {
    margin-right: auto !important;
}

.navbar {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: .5rem 1rem;
    background-color: #fcfcfc;
}

.navbar-brand {
    width: 60px;
}

.navbar-items {
    background-color: orangered;
    font-size: 20px;
    border-radius: 10px;
    padding: 0px 100px 0px 100px;
    padding-left: 0;
    padding-right: 0;
}

.brand {
    display: block;
    margin: auto;
    align-items: right;
    position: relative;
}

.logostyle {
    fill:none;
    stroke: #ffc93c;
    stroke-width: 35px;
    stroke-miterlimit: 10px;
}